import React from 'react';
import { Container, Flex, Stack, Text } from '@chakra-ui/react';

const Footer = () => {
  return (
    <Container maxW={'7xl'}>
      <Flex
        flexWrap="wrap"
        justifyContent="space-between"
        alignContent="center"
        mt={10}
        mb={5}
        backgroundColor="black"
        borderRadius="10px"
        textColor="white"
        height="43px"
      >
        <marquee>© 2024 XPENSO App. All rights reserved by XPENSO Team  </marquee>
      </Flex>
    </Container>
  );
};

export default Footer;
